"use client";

import { useState } from "react";

interface UserProfile {
  full_name?: string;
  age?: number;
  weight?: number;
  height?: number;
  gender?: "male" | "female";
  activity_level?:
    | "sedentary"
    | "light"
    | "moderate"
    | "active"
    | "very_active";
  goal?: "lose" | "maintain" | "gain";
  daily_calorie_goal?: number;
}

interface Recipe {
  name: string;
  description: string;
  prep_time: number;
  cook_time: number;
  servings: number;
  calories_per_serving: number;
  protein_per_serving: number;
  carbs_per_serving: number;
  fat_per_serving: number;
  ingredients: string[];
  instructions: string[];
  tags: string[];
}

interface RecipeGeneratorProps {
  userProfile?: UserProfile;
}

export default function RecipeGenerator({ userProfile }: RecipeGeneratorProps) {
  const [preferences, setPreferences] = useState("");
  const [mealType, setMealType] = useState("orice");
  const [timeLimit, setTimeLimit] = useState("");
  const [ingredients, setIngredients] = useState("");
  const [loading, setLoading] = useState(false);
  const [recipe, setRecipe] = useState<Recipe | null>(null);
  const [error, setError] = useState("");

  const generateRecipe = async () => {
    if (!preferences.trim()) {
      setError("Te rog sƒÉ descrii ce fel de re»õetƒÉ vrei");
      return;
    }

    setLoading(true);
    setError("");
    setRecipe(null);

    try {
      const response = await fetch("/api/recipe-generator", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          preferences: preferences.trim(),
          meal_type: mealType,
          time_limit: timeLimit ? parseInt(timeLimit) : null,
          available_ingredients: ingredients.trim(),
          user_profile: userProfile,
        }),
      });

      const data = await response.json();

      if (data.success) {
        setRecipe(data.recipe);
      } else {
        setError(data.error || "Nu am putut genera re»õeta");
      }
    } catch {
      setError("A apƒÉrut o eroare. Te rog sƒÉ √Æncerci din nou.");
    } finally {
      setLoading(false);
    }
  };

  const mealTypes = [
    { value: "orice", label: "Orice fel de masƒÉ" },
    { value: "mic_dejun", label: "Mic dejun" },
    { value: "pranz", label: "Pr√¢nz" },
    { value: "cina", label: "CinƒÉ" },
    { value: "gustare", label: "Gustare" },
  ];

  const examplePrompts = [
    "O salatƒÉ sƒÉnƒÉtoasƒÉ cu pui »ôi avocado",
    "Ceva rapid pentru mic dejun cu ouƒÉ",
    "O supƒÉ caldƒÉ »ôi hrƒÉnitoare pentru seara",
    "Un smoothie proteic pentru dupƒÉ antrenament",
    "O masƒÉ vegetarianƒÉ bogatƒÉ √Æn proteine",
  ];

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white rounded-lg shadow-sm border p-6">
        <h2 className="text-2xl font-bold text-gray-800 mb-2">
          Generator de re»õete
        </h2>
        <p className="text-gray-600">
          Descrie ce vrei sƒÉ gƒÉte»ôti »ôi √Æ»õi voi genera o re»õetƒÉ personalizatƒÉ
        </p>
      </div>

      {/* Form */}
      <div className="bg-white rounded-lg shadow-sm border p-6">
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Ce ai vrea sƒÉ gƒÉte»ôti? *
            </label>
            <textarea
              value={preferences}
              onChange={(e) => setPreferences(e.target.value)}
              placeholder="De exemplu: ceva sƒÉnƒÉtos cu pui, o supƒÉ de legume, un desert fƒÉrƒÉ zahƒÉr..."
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none"
              rows={3}
            />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Tipul mesei
              </label>
              <select
                value={mealType}
                onChange={(e) => setMealType(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                {mealTypes.map((type) => (
                  <option key={type.value} value={type.value}>
                    {type.label}
                  </option>
                ))}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Timp maxim (minute)
              </label>
              <input
                type="number"
                value={timeLimit}
                onChange={(e) => setTimeLimit(e.target.value)}
                placeholder="ex: 30"
                min="5"
                max="240"
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Ingrediente disponibile (op»õional)
            </label>
            <input
              type="text"
              value={ingredients}
              onChange={(e) => setIngredients(e.target.value)}
              placeholder="ex: pui, broccoli, orez, ro»ôii..."
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
          </div>

          {error && (
            <div className="bg-red-50 border border-red-200 rounded-md p-3">
              <p className="text-sm text-red-600">{error}</p>
            </div>
          )}

          <button
            onClick={generateRecipe}
            disabled={loading || !preferences.trim()}
            className="w-full bg-blue-600 text-white py-3 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {loading ? (
              <div className="flex items-center justify-center">
                <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                Generez re»õeta...
              </div>
            ) : (
              "üë®‚Äçüç≥ GenereazƒÉ re»õeta"
            )}
          </button>
        </div>

        {/* Example prompts */}
        {!recipe && (
          <div className="mt-6 pt-6 border-t">
            <p className="text-sm text-gray-600 mb-3">Idei de re»õete:</p>
            <div className="flex flex-wrap gap-2">
              {examplePrompts.map((prompt, index) => (
                <button
                  key={index}
                  onClick={() => setPreferences(prompt)}
                  className="text-xs bg-gray-100 border border-gray-200 rounded-full px-3 py-2 hover:bg-gray-200 transition-colors"
                >
                  {prompt}
                </button>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Recipe Result */}
      {recipe && (
        <div className="bg-white rounded-lg shadow-sm border overflow-hidden">
          {/* Recipe Header */}
          <div className="bg-gradient-to-r from-blue-500 to-purple-600 text-white p-6">
            <h3 className="text-2xl font-bold mb-2">{recipe.name}</h3>
            <p className="text-blue-100">{recipe.description}</p>

            <div className="flex flex-wrap gap-4 mt-4 text-sm">
              <div className="flex items-center">
                <span className="mr-1">‚è±Ô∏è</span>
                <span>Prep: {recipe.prep_time} min</span>
              </div>
              <div className="flex items-center">
                <span className="mr-1">üî•</span>
                <span>GƒÉtire: {recipe.cook_time} min</span>
              </div>
              <div className="flex items-center">
                <span className="mr-1">üë•</span>
                <span>{recipe.servings} por»õii</span>
              </div>
            </div>
          </div>

          {/* Nutrition Info */}
          <div className="p-6 bg-gray-50 border-b">
            <h4 className="font-medium text-gray-800 mb-3">
              Informa»õii nutri»õionale (per por»õie):
            </h4>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
              <div>
                <div className="text-2xl font-bold text-blue-600">
                  {Math.round(recipe.calories_per_serving)}
                </div>
                <div className="text-sm text-gray-600">kcal</div>
              </div>
              <div>
                <div className="text-xl font-bold text-red-600">
                  {Math.round(recipe.protein_per_serving * 10) / 10}g
                </div>
                <div className="text-sm text-gray-600">Proteine</div>
              </div>
              <div>
                <div className="text-xl font-bold text-yellow-600">
                  {Math.round(recipe.carbs_per_serving * 10) / 10}g
                </div>
                <div className="text-sm text-gray-600">Carbohidra»õi</div>
              </div>
              <div>
                <div className="text-xl font-bold text-green-600">
                  {Math.round(recipe.fat_per_serving * 10) / 10}g
                </div>
                <div className="text-sm text-gray-600">GrƒÉsimi</div>
              </div>
            </div>
          </div>

          <div className="p-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
              {/* Ingredients */}
              <div>
                <h4 className="font-medium text-gray-800 mb-4 flex items-center">
                  <span className="mr-2">üõí</span>
                  Ingrediente:
                </h4>
                <ul className="space-y-2">
                  {recipe.ingredients.map((ingredient, index) => (
                    <li key={index} className="flex items-start">
                      <span className="text-blue-500 mr-2">‚Ä¢</span>
                      <span className="text-gray-700">{ingredient}</span>
                    </li>
                  ))}
                </ul>
              </div>

              {/* Instructions */}
              <div>
                <h4 className="font-medium text-gray-800 mb-4 flex items-center">
                  <span className="mr-2">üìù</span>
                  Instruc»õiuni:
                </h4>
                <ol className="space-y-3">
                  {recipe.instructions.map((instruction, index) => (
                    <li key={index} className="flex items-start">
                      <span className="bg-blue-500 text-white rounded-full w-6 h-6 flex items-center justify-center text-sm font-medium mr-3 mt-0.5 flex-shrink-0">
                        {index + 1}
                      </span>
                      <span className="text-gray-700">{instruction}</span>
                    </li>
                  ))}
                </ol>
              </div>
            </div>

            {/* Tags */}
            {recipe.tags && recipe.tags.length > 0 && (
              <div className="mt-8 pt-6 border-t">
                <h4 className="font-medium text-gray-800 mb-3">Tags:</h4>
                <div className="flex flex-wrap gap-2">
                  {recipe.tags.map((tag, index) => (
                    <span
                      key={index}
                      className="bg-blue-100 text-blue-800 text-sm px-3 py-1 rounded-full"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
              </div>
            )}

            {/* Action buttons */}
            <div className="mt-8 pt-6 border-t flex gap-3">
              <button
                onClick={() => setRecipe(null)}
                className="flex-1 bg-gray-200 text-gray-800 py-2 px-4 rounded-md hover:bg-gray-300 transition-colors"
              >
                GenereazƒÉ altƒÉ re»õetƒÉ
              </button>
              <button
                onClick={() => {
                  const text = `${recipe.name}\n\n${
                    recipe.description
                  }\n\nIngrediente:\n${recipe.ingredients.join(
                    "\n"
                  )}\n\nInstruc»õiuni:\n${recipe.instructions
                    .map((inst, i) => `${i + 1}. ${inst}`)
                    .join("\n")}`;
                  navigator.clipboard.writeText(text);
                  alert("Re»õeta a fost copiatƒÉ √Æn clipboard!");
                }}
                className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition-colors"
              >
                üìã CopiazƒÉ re»õeta
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
